#ContainerContactPage
{
    width: 100%;
    display: grid;
    justify-content: center;
}
#guillemetsContacts{
    position: relative;
    animation: clignote 1s normal 2;
    animation-delay : 1s;
    z-index: 2;
}

#soulignageContacts{
    position: relative;
    z-index: 1;
}

#soulignageLinks{
    position: relative;
    z-index: 1;
}

#TitreContactsWhGuillemets{
    display: inline-grid;
    grid-template-columns: auto auto ;
}



#TitreContacts{
    font-family: Briberra, cursive;
    font-size: 7.2vh;
    color: black;
    text-align: left;
    text-indent: 5%;
    z-index: 3;

}

#TitreLinks{
    font-family: Briberra, cursive;
    color: black;
    font-size: 7.2vh;
    z-index: 2;

}

#Contacts{
    display: grid;
    justify-content: center;
}

#SocialNetworks{
    display: grid;
    justify-content: center;
}

#SocialNetworksLinks-container{
    display: flex;
    justify-content: space-around;
    align-content: center;
    align-items: center;
}

@media only screen and (max-width: 767px) {
    #guillemetsContacts{
        width: 5vh;
        left: -28vw;
        top:2.4vh;
    }

    #soulignageContacts{
        width: 13vh;
        top:-5.5vh;
    }

    #soulignageLinks{
        width: 10vh;
        height: 2.5vh;
        top:1.2vh;
        left:0.8vw;
    }

    #SocialNetworks{
        grid-template-rows: 5vh 6vh;
    }

    #SocialNetworksLinks-container{
        width: 80vw;
    }

    #ContainerContactPage {
        height: 80vh;
        grid-template-columns: 90vw;
        grid-template-areas:
                "a"
                "b";
    }

    #Contacts{
        margin-top: 2vh;
        grid-template-rows: 8vh auto auto auto;
        grid-area: a;
        grid-gap: 3vh;
    }

    #SocialNetworks{
        grid-area: b;
    }

    #TitreContacts{
        font-size: 5vh;
    }

    #TitreLinks{
        font-size: 5vh;
        text-align: left;
        text-indent:5%
    }

    #SocialNetworksLinks-container{
        grid-gap: 1vh;
    }
    .SocialNetworkIcon_color{
        width: 15vw;
    }
    #CentraleMarseille_color{
        width: 20vw;
    }
}

@media only screen and (min-width: 768px) {
    #guillemetsContacts{
        width: 6vh;
        left: -12.8vw;
        top:4vh;
    }
    #soulignageContacts{
        width: 20vh;
        top:-7.8vh;
    }

    #soulignageLinks{
        width: 13vh;
        height: 3vh;
        top:-7.7vh;
        left:1.7vw;
    }


    #SocialNetworksLinks-container{
        flex-direction: column;
        height: 75%;
        grid-gap: 3vh;
    }
    .SocialNetworkIcon_color{
        width: 6vw;

    }
    #CentraleMarseille_color{
        width: 10vw;
    }
    #SocialNetworks{
        grid-template-rows: 17vh ;
    }
    #ContainerContactPage
    {
        grid-template-areas: "a b";
        grid-template-columns: 17vw 45vw;
    }

    #Contacts{
        grid-template-rows: 10vh auto auto auto;
        grid-area: b;
        height:60vh;
        grid-gap: 5vh;
        margin-left: 20vw;
    }

    #SocialNetworks{
        grid-area: a;
    }
}

